html {
  font-family: "Helvetica Neue", sans-serif; }

h1,
h2,
p {
  margin: 0; }

.mb-1 {
  margin-bottom: 8px; }

.container {
  margin: 50px auto;
  width: 1280px; }

.multiplicationChart {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 80px 85px;
  background-color: #f0f0f0;
  border: solid 1px #d8d8d8; }
  .multiplicationChart__col {
    width: 350px;
    height: 366px; }
    .multiplicationChart__col:not(:first-child) {
      background-color: #fff;
      border-radius: 100px 0 30px;
      box-shadow: 0 3px 10px #d8d8d8; }
    .multiplicationChart__col:nth-child(-n+6) {
      margin-bottom: 40px; }
  .multiplicationChart__title {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    .multiplicationChart__title__header, .multiplicationChart__title__footer {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .multiplicationChart__title__header .line, .multiplicationChart__title__footer .line {
        width: 278px;
        border: solid 1px #2eb738; }
      .multiplicationChart__title__header::before, .multiplicationChart__title__header::after, .multiplicationChart__title__footer::before, .multiplicationChart__title__footer::after {
        content: "x";
        font-size: 24px;
        color: #2eb738; }
    .multiplicationChart__title__body {
      text-align: center;
      color: #2eb738; }
      .multiplicationChart__title__body h1 {
        font-size: 56px; }
      .multiplicationChart__title__body h2 {
        font-size: 22px; }
  .multiplicationChart__content {
    box-sizing: border-box;
    padding: 64px 40px;
    font-size: 24px;
    color: #2eb738; }
    .multiplicationChart__content__left, .multiplicationChart__content__right {
      width: 118px;
      letter-spacing: 1px; }
    .multiplicationChart__content__left {
      float: left;
      margin-right: 34px; }
      .multiplicationChart__content__left .topic {
        margin-top: -10px;
        margin-bottom: 14px;
        line-height: 1;
        font-size: 128px;
        font-weight: bold;
        text-align: center;
        text-shadow: 4px 3px 0 #f0f0f0; }
    .multiplicationChart__content__right {
      float: right; }
    .multiplicationChart__content::after {
      content: "";
      display: block;
      clear: both; }

footer {
  padding: 8px 85px;
  text-align: right;
  background-color: #2eb738; }
  footer p {
    font-size: 16px;
    color: #f0f0f0; }
